import React from "react";
import styles from "./Header.module.scss";
import facebook from "./img/facebook.svg";
import gmail from "./img//gmail.svg";
import hangsout from "./img/hangsout.svg";
import twitter from "./img/twitter.svg";
import instagram from "./img/instagram.svg";
import BasketCard from "../CardInBasket/CardInBasket";
import classNames from "classnames";
import Favorites from "../Favorites/Favorites";
import PropTypes from "prop-types";
import Navigation from "../Navigation/Navigation";
import Button from "../Button/Button";
import { useLocation, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setIsClose, setIsOpen } from "../../store/shoppingCart/shoppingCart";

const Header = (props) => {
  const navigate = useNavigate();
  const location = useLocation();
  const itemsInCart = useSelector((state) => state.goods.goods);
  const cartCounter = useSelector((state) => state.goodsAtCartCounter.counter);
  const headerCart = useSelector((state) => state.headerCart.isOpen);
  const isLoaded = useSelector((state) => state.goods.isLoaded);
  const dispatch = useDispatch();
  const {
    body,
    socialIcons,
    container,
    line,
    loginCartWrapper,
    shoppingBasket,
    goodsAtCartCount,
    shoppingBasketWrapper,
  } = styles;
  return (
    <header>
      <div className={line}></div>
      <section className={body}>
        <div className={container}>
          <div className={socialIcons}>
            <a href="#">
              <img src={facebook} alt="facebook__icon"></img>
            </a>
            <a href="#">
              <img src={gmail} alt="facebook__icon"></img>
            </a>

            <a href="#">
              <img src={hangsout} alt="facebook__icon"></img>
            </a>

            <a href="#">
              <img src={twitter} alt="facebook__icon"></img>
            </a>

            <a href="#">
              <img src={instagram} alt="facebook__icon"></img>
            </a>
          </div>
        </div>
        <Navigation />
        <div className={loginCartWrapper}>
          <Favorites />
          <button>login / Register</button>
          <div className={shoppingBasketWrapper}>
            <img
              onClick={() => {
                headerCart ? dispatch(setIsClose()) : dispatch(setIsOpen());
              }}
              src="./images/cart/shopping-cart.png"
              alt="basket_image"
            ></img>
            <p className={goodsAtCartCount}>{cartCounter}</p>
            <div
              className={classNames(shoppingBasket)}
              style={{ display: headerCart ? "block" : "none" }}
            >
              {isLoaded &&
                itemsInCart.map(
                  (el) =>
                    el.cart === true && (
                      <BasketCard
                        key={el.EAN}
                        id={el.EAN}
                        item={el}
                        qty={el.qtyInOrder}
                        needNavigateBtn={true}
                      />
                    )
                )}

              {location.pathname !== "/cart" && cartCounter !== 0 && (
                <Button
                  text={"Proceed to cart"}
                  backgroundColor={"white"}
                  handleClick={(e) => {
                    navigate("./cart");
                  }}
                />
              )}
            </div>
          </div>
        </div>
      </section>
    </header>
  );
};

export default Header;

Header.propTypes = {
  itemsInCart: PropTypes.array,
  deleteFromCartClickHandler: PropTypes.func,
  showHideBasket: PropTypes.func,
  displayCart: PropTypes.bool,
  goodsAtCart: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  inFavorites: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
};

Header.defaultProps = {
  itemsInCart: [],
  deleteFromCartClickHandler: () => {},
  showHideBasket: () => {},
  displayCart: false,
  goodsAtCart: 0,
  inFavorites: 0,
};
